#HeaderItem{
	display: flex;
	flex: 1 1 auto;
	background-color: white;
	border-radius: 15px 15px;
	margin: 0px 25px;
	height: 75px;
	justify-content: center;
	align-items: center;
	color: #596b5d;
	font-size: 3vmin;
	width: 20vmin;
	text-align: center;
	cursor: pointer;
}

#hiatusItems{
	display: flex;
	margin-top: 20px;
}
@media only screen and (max-width: 1023px) {
	#HeaderItem{
		font-size: 5vmin;
		width: fit-content;
		height: 10vmin;
		margin-top: 2vh;
	}
}

@media only screen and (max-width: 767px) {
	#HeaderItem{
		font-size: 3vmin;
		height: 10vmin;
		width: 15vmax;
		margin-top: 2vh;
	}
}

@media only screen and (max-width: 540px) {
	#HeaderItem{
		font-size: 2.8vmin;
		height: 10vmin;
		width: 12.5vmax;
		margin-top: 2vh;
		margin: 10px 10px;
	}
}
