body{
  background-image: url(../../../static/backgrounds/hiatus/hiatusBackground.svg);
}
#hiatusBody {
  display: flex;
  flex-direction: column;
  font-family: couture;
}
#hiatusBody > img {
  margin: -200px 0;
  height: 500px;
  align-self: flex-end;
}
#hiatusBody > div {
  display: flex;
  flex-direction: column;
}
.hiatus-text {
  color: white;
  font-size: 5vmin;
  margin: 0 20px;
  text-align: center;
  word-break: keep-all;
  float: none;
}

#line {
  width: 100vw;
  float: left;
  height: 8px;
  background: white;
  border: none;
}
/* normal phone devices */
@media only screen and (max-width: 767px) and (orientation: portrait){
  body{
    background-image: url(../../../static/backgrounds/hiatus/wave.svg);
    background-size: cover;
  }
  #line {
    width: 70%;
    float: left;
    height: 8px;
    background: white;
    border: none;
  }
  .hiatus-text {
    color: white;
    font-size: 6vmin;
    margin: 0 20px;
    text-align: center;
    word-break: keep-all;
    float: none;
  }
  #hiatusBody > img {
    margin: 0 0;
    height: 43vh;
    align-self: center;
  }
}

/* ipad pro*/
@media only screen and (min-device-width: 1024px) and (max-device-height: 1366px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
  #hiatusBody > img {
    margin: -30px 0px;
    height: 44vh;
    align-self: center;
  }
}
/* ipad pro */
@media only screen and (min-device-width: 1366px) and (max-device-height: 1024px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  #hiatusBody > img {
    margin: -100px 0px;
    height: 41vh;
  }
}

@media only screen and (min-device-width: 481px) and (max-device-width: 1024px) and (orientation: landscape) {
  #hiatusBody > img {
    margin: -50px 0px;
    height: 29vh;
  }
}
